@import "./fonts.scss";

html {
	box-sizing: border-box;
}

*,
::before,
::after {
	box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
ul,
ol,
li,
dd,
dt {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
}

ul {
	list-style: none;
}

a {
	margin: 0;
	padding: 0;
	font-size: 100%;
	vertical-align: baseline;
	color: inherit;
	text-decoration: none;
	background: transparent;
}

*::-webkit-scrollbar {
	width: 3px;
}

*::-webkit-scrollbar-track {
	box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

*::-webkit-scrollbar-thumb {
	background-color: darkgrey;
	outline: 1px solid slategrey;
}

body {
	font-family: "SFProDisplay";
	font-style: normal;
	font-weight: 400;
	font-size: 20px;
	line-height: 1.2;
}

.container {
	max-width: 1200px;
	padding: 0 15px;
	margin: 0 auto;
}
